@import url(https://fonts.googleapis.com/css2?family=Comfortaa:wght@700&display=swap);
* {
  box-sizing: border-box;
}

*::-webkit-scrollbar {
  width: 4px;
  height: 80%;
  background-color: rgb(0, 5, 34);
  border-radius: 50%;
}

*::-webkit-scrollbar-thumb {
  background-color: white;
  border-radius: 50%;
}

body, html {
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  
  background-color: black;
  background-image: linear-gradient(rgb(0, 2, 12), rgb(0, 5, 34));
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.detail-container {
    min-width: 150px;
    max-width: 250px;
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: space-between;
    align-self: stretch;
    text-shadow: 1.5px 1.5px 2px black;
    border-bottom: 1px solid rgb(185, 185, 185);
}

.container-title {
    margin: calc(5px + 0.5vmin) 0;
    padding: 0;
    display: inline-block;
    color: rgb(218, 218, 218);
    font-family: 'Comfortaa', cursive;
    font-size: calc(10px + 1vmin);
}

.container-value {
    margin: calc(5px + 0.5vmin) 0;
    padding: 0 calc(8px + 1vmin);
    display: inline-block;
    color: rgb(218, 218, 218);
    font-family: 'Comfortaa', cursive;
    font-size: calc(10px + 1vmin);
}

@media only screen and (max-width: 480px) {
    .detail-container {
        max-width: 100%
    }

    .detail-container:last-child {
        border: none;
    }
}

@media only screen and (max-width: 768px) {
    .detail-container {
        max-width: 100%;
    }

    .detail-container:last-child {
        border: none;
    }
}
.container {
    margin: 0;
    padding: 0 18px;
    width: 100%;
    height: 100%;
    /* opacity: 0;
    transform: translate(10px, 0);
    animation: fadein 0.5s ease-out 0.3s 1 forwards; */
    overflow: auto;
}

.container-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.city-name {
    margin: 0;
    padding: 0;
    color: white;
    font-family: 'Comfortaa', cursive;
    font-size: calc(20px + 2vmin);
    letter-spacing: 2px;
}

.container-temp {
    display: flex;
    align-items: baseline;
    justify-content: flex-start;
    flex-direction: row;
    text-shadow: 1.5px 1.5px 2px black;
    border-bottom: 1px solid white;
}

.temp {
    margin: 0;
    padding: 0;
    color: rgb(218, 218, 218);
    font-family: 'Comfortaa', cursive;
    font-size: calc(10px + 8vmin);
}

.temp-min-max {
    margin: 0;
    margin-left: 8px;
    padding-left: 8px;
    color: rgb(218, 218, 218);
    font-family: 'Comfortaa', cursive;
    font-size: calc(10px + 2vmin);
    border-left: 1px solid white;
}

.description {
    flex-grow: 1;
    text-align: end;
    margin: 0;
    margin-left: 20px;
    margin-right: 26px;
    padding: 0;
    color: rgb(218, 218, 218);
    font-family: 'Comfortaa', cursive;
    font-size: calc(10px + 1vmin);
}

.description::first-letter {
    text-transform: capitalize;
}

.container-details {
    height: 200px;
    margin-top: calc(10px + 1vmin);
    padding: 0;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    justify-content: flex-start;
    overflow: auto;
}

@media only screen and (max-width: 480px) {
    .container-temp {
        align-items: center;
    }

    .container-details {
        height: -webkit-max-content;
        height: max-content;
        flex-wrap: nowrap;
    }
}

@media only screen and (max-width: 768px) {
    .container-temp {
        align-items: center;
    }

    .temp {
        font-size: calc(30px + 1vmin);
    }

    .container-details {
        max-width: 100%;
        flex-wrap: nowrap;
    }
}

@-webkit-keyframes fadein {
    from {
        opacity: 0;
        -webkit-transform: translate(10px, 0);
                transform: translate(10px, 0);
    }
    to {
        opacity: 1;
        -webkit-transform: translate(0, 0);
                transform: translate(0, 0);
    }
}

@keyframes fadein {
    from {
        opacity: 0;
        -webkit-transform: translate(10px, 0);
                transform: translate(10px, 0);
    }
    to {
        opacity: 1;
        -webkit-transform: translate(0, 0);
                transform: translate(0, 0);
    }
}
.header {
    margin: 0 4%;
}

.header-app-name {
    margin: 0;
    padding: 10px 0 0 0;
    color: white;
    font-family: 'Comfortaa', cursive;
    font-size: calc(10px + 4vmin);
}

.content {
    width: 100%;
    height: 70vh;
    padding: 0 4%;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

.content-getWeather {
    width: 400px;
    margin-right: 18px;
}

.getWeather-moto {
    margin: 0;
    padding: 0;
    color: white;
    font-family: 'Comfortaa', cursive;
    font-size: calc(10px + 6vmin);
}

.input {
    width: 100%;
    margin-top: calc(10px + 3vmin);
    padding-bottom: 1.5vmin;
    background-color: transparent;
    outline: none;
    border: none;
    color: white;
    font-size: calc(10px + 2vmin);
    border-bottom: 2px solid white;
}

.content-weather {
    width: 650px;
    height: 90%;
    background: rgba( 255, 255, 255, 0.25 );
    backdrop-filter: blur(4px);
    -webkit-backdrop-filter: blur(4px);
    border-radius: 10px;
    border: 1px solid rgba( 255, 255, 255, 0.18 );
    opacity: 0;
    -webkit-transform: translate(10px, 0);
            transform: translate(10px, 0);
    -webkit-animation: fadein 0.5s ease-out 0.3s 1 forwards;
            animation: fadein 0.5s ease-out 0.3s 1 forwards;
    overflow: auto;
}

.card-appear {
    opacity: 0;
}

.card-appear.card-appear-active {
    opacity: 1;
    transition: all 600ms ease-in;
}

.card-enter {
    opacity: 0;
}

.card-enter.card-enter-active {
    opacity: 1;
    transition: all 600ms ease-in;
}

.card-exit {
    opacity: 1;
}

.card-exit.card-exit-active {
    display: none;
    transition: all 0ms ease-in;
}
.card-exit-done {
    display: none;
}

.image-clouds {
    width: 400px;
    position: fixed;
    bottom: 0;
    right: 0;
    z-index: -1;
}

@media only screen and (max-width: 480px) {
    .header {
        margin: 0;
    }
    
    .header-app-name {
        padding: 10px 0 0 0;
        font-size: calc(10px + 6vmin);
        text-align: center;
    }

    .content {
        margin: 0;
        margin-top: 20px;
        padding: 8px;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

    .content-getWeather {
        width: 100%;
        margin-right: 0;
    }

    .content-weather {
        width: 100%;
        margin: 10px;
    }
}

@-webkit-keyframes fadein {
    from {
        opacity: 0;
        -webkit-transform: translate(10px, 0);
                transform: translate(10px, 0);
    }
    to {
        opacity: 1;
        -webkit-transform: translate(0, 0);
                transform: translate(0, 0);
    }
}

@keyframes fadein {
    from {
        opacity: 0;
        -webkit-transform: translate(10px, 0);
                transform: translate(10px, 0);
    }
    to {
        opacity: 1;
        -webkit-transform: translate(0, 0);
                transform: translate(0, 0);
    }
}
